import React, { useState, useEffect } from 'react'
import './Login.css'
import { Link } from 'react-router-dom'
import { AppContext } from '../App'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Meta from './Meta'

function Login({ history, match, location }) {
  const store = React.useContext(AppContext)
  let browser = ''
  const agent = navigator.userAgent.toLowerCase()
  if (agent.indexOf('chrome') > -1) browser = 'chrome'
  else if (agent.indexOf('safari') > -1) browser = 'safari'

  const [checkUsername, setCheckUsername] = useState('')
  const [checkPassword, setCheckPassword] = useState('')
  const [checkPasswordConfirm, setCheckPasswordConfirm] = useState('')
  const [buttonName, setButtonName] = useState('Î°úÍ∑∏Ïù∏')
  const [userinfo, setUserinfo] = useState()

  useEffect(() => {
    //Î°úÍ∑∏Ïù∏ÌôîÎ©¥ÏóêÏÑú Î°úÍ∑∏Ïù∏ÏùÄ ÏïàÎêòÏñ¥ÏûàÎäîÎç∞ Ïú†Ï†ÄÏ†ïÎ≥¥Í∞Ä ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨
    const userinfoElem = document.querySelector('#userinfo')
    if (location.pathname === '/login' || location.pathname === '/login/' || location.pathname === '/register' || location.pathname === '/register/') {
      if (!store.login) {
        let frame = setInterval(function () {
          console.log('Ïú†Ï†ÄÏ†ïÎ≥¥ Ï≤¥ÌÅ¨')
          if (userinfoElem && userinfoElem.value) {
            //userinfoÍ∞Ä ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨
            //ÏÉàÎ°úÍ≥†Ïπ®ÎêòÍ±∞ÎÇò, Î°úÍ∑∏Ïù∏ Î≤ÑÌäºÏùÑ ÎàÑÎ•¥Î©¥ userinfoÎ•º Í∞ÄÏ†∏Ïò¥
            clearInterval(frame) //userinfo ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏ Ï≤¥ÌÅ¨ Ï§ëÏßÄ
            let user = JSON.parse(userinfoElem.value)

            if (user.host === 'naver') {
              let email = user.email
              let username
              if (email) {
                username = email.substring(0, email.indexOf('@')) + '_n'
                if (username === 'popping2606_n') username = 'imki123' //ÎÇ¥ÏïÑÏù¥Îîî
                console.log('ÎÑ§Ïù¥Î≤Ñ Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ:', username)
                user = {
                  username: username,
                  email: email,
                  imageUrl: user.profile_image,
                  host: user.host,
                }
              } else {
                console.log('Ïù¥Î©îÏùº Ï†ïÎ≥¥ ÏóÜÏùå. Î°úÍ∑∏ÏïÑÏõÉ.')
                alert('Ïù¥Î©îÏùºÏù¥ ÏóÜÏúºÎ©¥ Î°úÍ∑∏Ïù∏Ïù¥ Î∂àÍ∞ÄÌï¥Ïöî. Ïù¥Î©îÏùº ÎèôÏùò Î∂ÄÌÉÅÎìúÎ†§Ïöî üòÑ')
                //ÎÑ§Ïù¥Î≤Ñ Î°úÍ∑∏ÏïÑÏõÉ
                const naverLogout = document.querySelector('#naverLogout')
                if (naverLogout) naverLogout.click()
                userinfoElem.value = ''
                history.replace()
                return
              }
            } else if (user.host === 'kakao') {
              let email = user.email
              let username
              if (email) {
                username = email.substring(0, email.indexOf('@')) + '_k'
                if (username === 'popping2606_k') username = 'imki123' //ÎÇ¥ÏïÑÏù¥Îîî
                console.log('Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ:', username)
                user = {
                  username: username,
                  email: email,
                  imageUrl: user.profile.thumbnail_image_url,
                  host: user.host,
                }
              } else {
                console.log('Ïù¥Î©îÏùº Ï†ïÎ≥¥ ÏóÜÏùå. Î°úÍ∑∏ÏïÑÏõÉ.')
                alert('Ïù¥Î©îÏùºÏù¥ ÏóÜÏúºÎ©¥ Î°úÍ∑∏Ïù∏Ïù¥ Î∂àÍ∞ÄÌï¥Ïöî. Ïù¥Î©îÏùº ÎèôÏùò Î∂ÄÌÉÅÎìúÎ†§Ïöî üòÑ')
                //Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏ÏïÑÏõÉ
                const kakaoLogout = document.querySelector('#kakaoLogout')
                if (kakaoLogout) kakaoLogout.click()
                userinfoElem.value = ''
                history.replace()
                return
              }
            } else {
              let email = user.email
              let username = email.substring(0, email.indexOf('@')) + '_g'
              if (username === 'popping2606_g') username = 'imki123' //ÎÇ¥ÏïÑÏù¥Îîî
              console.log('Íµ¨Í∏Ä Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ:', username)
              user = {
                username: username,
                email: email,
                name: user.name,
                imageUrl: user.imageUrl,
                host: user.host,
              }
            }
            //console.log(user)

            let url = process.env.REACT_APP_URL + '/auth/oauth'
            //url = process.env.REACT_APP_LOCAL_URL + '/auth/oauth'
            //Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µÏãú ÌÜ†ÌÅ∞Ïóê name, email, imageUrl Ï†ÄÏû•
            fetch(url, {
              mode: 'cors',
              method: 'POST',
              credentials: 'include',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify(user),
            })
              .then((res) => {
                if (res.status === 200 || res.status === 201) {
                  //ÏÑ±Í≥µÌïòÎ©¥ ÏïÑÎûò then ÏûëÎèô
                  res.json().then((res) => {
                    alert(res.username + 'Îãò ÌôòÏòÅÌï©ÎãàÎã§ üòÑ')
                    setUserinfo(res)
                    //Î°úÍ∑∏Ïù∏ Ïãú ÌôàÏúºÎ°ú Ïù¥Îèô
                    history.replace('/')
                    //history.go(-1) //Îí§Î°úÍ∞ÄÍ∏∞
                  })
                } else {
                  let message = 'Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§ :('
                  if (res.status === 401) {
                    message += '\nÎ°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.'
                  }
                  console.log(message)
                }
              })
              .catch((e) => console.error(e))
          }
        }, 1000)
      }
    } else if (location.pathname === '/loginStatus' || location.pathname === '/loginStatus/') {
      if (userinfoElem && !userinfoElem.value) {
        setUserinfo(false)
      }
    }
  }, [store.login, location, history])

  useEffect(() => {
    //Ïú†Ï†ÄÏù∏Ìè¨Í∞Ä ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏Ï≤òÎ¶¨
    if (store) {
      store.setReady(true)
      if (userinfo) {
        store.setLogin(userinfo)
      }
    }
  })

  useEffect(() => {
    //Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Ïù¥ ÎêòÏñ¥ÏûàÎã§Î©¥ Ïä§ÌÖåÏù¥ÌÑ∞Ïä§Î°ú Ïù¥Îèô
    if (location.pathname === '/login' || location.pathname === '/login/') {
      if (store.login) {
        history.replace('/loginStatus')
      }
    }

    //Î°úÍ∑∏Ïù∏Ïù¥ ÏïàÎêòÏñ¥ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏ÏúºÎ°ú Ïù¥Îèô
    if (location.pathname === '/loginStatus' || location.pathname === '/loginStatus/') {
      //console.log(store.login)
      if (!store.login) {
        setTimeout(function () {
          if (!store.login) {
            history.replace('/login')
          }
        }, 1000)
      }
    }
    if (location.pathname.indexOf('register') > -1) {
      setButtonName('ÌöåÏõêÍ∞ÄÏûÖ')
    } else if (location.pathname.indexOf('withdraw') > -1) {
      setButtonName('ÌöåÏõêÌÉàÌá¥')
    } else {
      setButtonName('Î°úÍ∑∏Ïù∏')
    }
  }, [location, store.login, history])

  const changeUsername = (e) => {
    const pattern = /[^a-zA-Z0-9„Ñ±-„Öé„Öè-„Ö£Í∞Ä-Ìû£_]/g
    const value = (e.target.value = e.target.value.replace(/\s/g, '_').replace(pattern, ''))
    if (value.length < 3) {
      //3Í∏ÄÏûê Ïù¥ÏÉÅ
      setCheckUsername('- 3Ïûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî')
    } else if (value.length > 20) {
      //20Í∏ÄÏûê Ïù¥Ìïò
      setCheckUsername('- 20Ïûê Ïù¥ÌïòÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî')
    } else {
      setCheckUsername('')
    }
  }
  const changePassword = (e) => {
    const { value } = e.target
    const passwordConfirm = document.querySelector('[name=passwordConfirm]')

    //ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Î≥ÄÍ≤ΩÎêòÎ©¥ ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ÏùÄ Ï¥àÍ∏∞Ìôî
    if (passwordConfirm) {
      passwordConfirm.value = ''
      setCheckPasswordConfirm('')
    }

    if (value.length < 6) {
      //6Í∏ÄÏûê Ïù¥ÏÉÅ
      setCheckPassword('- 6Ïûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî')
    } else if (value.length > 20) {
      //20Í∏ÄÏûê Ïù¥Ìïò
      setCheckPassword('- 20Ïûê Ïù¥ÌïòÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî')
    } else {
      setCheckPassword('')
    }
  }
  const changePasswordConfirm = (e) => {
    const { value } = e.target
    const password = document.querySelector('[name=password]')

    if (value !== password.value && value.length >= 1) {
      //passwordÏôÄ Í∞ôÏúºÎ©¥
      setCheckPasswordConfirm('- ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§')
    } else {
      setCheckPasswordConfirm('')
    }
  }
  const clickButton = (e) => {
    e.preventDefault()
    let url = process.env.REACT_APP_URL + '/auth'
    //url = process.env.REACT_APP_LOCAL_URL+'/auth'

    const pattern = /[^a-zA-Z0-9Í∞Ä-Ìû£_]/g
    let username = document.querySelector('[name=username]')
    if (username) username = username.value = username.value.replace(pattern, '')
    let password = document.querySelector('[name=password]')
    if (password) password = password.value

    if (checkUsername === '' && password.length >= 1 && checkPassword === '' && checkPasswordConfirm === '') {
      //ÏûÖÎ†•ÌèºÏóê Ïù¥ÏÉÅÏù¥ ÏóÜÏúºÎ©¥ fetch submit
      if (buttonName === 'ÌöåÏõêÍ∞ÄÏûÖ') {
        url += '/register'
        fetch(url, {
          mode: 'cors',
          method: 'POST',
          credentials: 'include',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            username: username,
            password: password,
          }),
        })
          .then((res) => {
            if (res.status === 200 || res.status === 201) {
              //ÏÑ±Í≥µÌïòÎ©¥ ÏïÑÎûò then ÏûëÎèô
              res.json().then((res) => {
                alert(res.username + 'ÎãòÏùò ÌöåÏõêÍ∞ÄÏûÖÏóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§ üòÑ')
                history.go(-1)
              })
            } else {
              let message = 'ÌöåÏõêÍ∞ÄÏûÖÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§ :('
              if (res.status === 409) {
                message += '\nÏù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§.'
              }
              if (res.status === 400) {
                message += '\nÏïÑÏù¥ÎîîÎÇò ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.'
              }
              alert(message)
            }
          })
          .catch((e) => console.error(e))
      } else if (buttonName === 'ÌöåÏõêÌÉàÌá¥') {
        if (window.confirm('Í≥ÑÏ†ï ÌÉàÌá¥Ïãú Î≥µÍµ¨Ìï† Ïàò ÏóÜÏäµÎãàÎã§. Ï†ïÎßêÎ°ú ÌÉàÌá¥ÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
          url += '/withdraw'
          fetch(url, {
            mode: 'cors',
            method: 'DELETE',
            credentials: 'include',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              username: store.login.username,
              password: password,
            }),
          })
            .then((res) => {
              if (res.status === 200) {
                //ÏÇ≠Ï†úÌïòÎ©¥ Ok
                alert('Í≥ÑÏ†ïÏù¥ ÌÉàÌá¥ÎêòÏóàÏäµÎãàÎã§. Îòê Îì§Îü¨ Ï£ºÏÑ∏Ïöî üòÑ')
                history.push('/')
              } else {
                let message = 'ÌÉàÌá¥Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§ :('
                if (res.status === 204) {
                  message += '\nÏ°¥Ïû¨ÌïòÏßÄ ÏïäÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§.'
                }
                if (res.status === 401) {
                  message += '\nÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.'
                }
                alert(message)
              }
            })
            .catch((e) => console.error(e))
        }
      } else {
        //Î°úÍ∑∏Ïù∏
        url += '/login'
        fetch(url, {
          mode: 'cors',
          method: 'POST',
          credentials: 'include',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            username: username,
            password: password,
          }),
        })
          .then((res) => {
            if (res.status === 200 || res.status === 201) {
              //ÏÑ±Í≥µÌïòÎ©¥ ÏïÑÎûò then ÏûëÎèô
              res.json().then((res) => {
                store.setLogin(res)
                alert(res.username + 'Îãò ÌôòÏòÅÌï©ÎãàÎã§ üòÑ')
                history.go(-1)
              })
            } else {
              let message = 'Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§ :('
              if (res.status === 401) {
                message += '\nÎ°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.'
              }
              alert(message)
            }
          })
          .catch((e) => console.error(e))
      }
    } else {
      alert('ÏûÖÎ†• Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.')
    }
  }

  const naverLogin = (e) => {
    const naverIdLogin = document.querySelector('#naverIdLogin')
    if (naverIdLogin) {
      console.log('ÎÑ§Ïù¥Î≤Ñ Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠')
      naverIdLogin.firstChild.click()
    }
  }
  const kakaoLogin = (e) => {
    const kakaoLogin = document.querySelector('#kakaoLogin')
    if (kakaoLogin) {
      console.log('Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠')
      kakaoLogin.click()
    }
  }
  const googleLogin = (e) => {
    const googleLogin = document.querySelector('#googleLogin')
    if (googleLogin) {
      console.log('Íµ¨Í∏Ä Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠')
      googleLogin.firstChild.firstChild.click()
    }
  }

  return (
    <div id="background">
      <div id="loginWrapper">
        <Meta
          data={{
            title: 'ÏûÑÍ∏∞Ïùò ÏΩîÎî© Î∏îÎ°úÍ∑∏',
            description: 'ÎÑ§Ïù¥Î≤Ñ, Ïπ¥Ïπ¥Ïò§, Íµ¨Í∏Ä Í≥ÑÏ†ïÏúºÎ°ú Î°úÍ∑∏Ïù∏ Ìï¥Î≥¥ÏÑ∏Ïöî!',
            canonical: `login`
          }}
        />
        {location.pathname.indexOf('/loginStatus') > -1 ? (
          <>
            {store.login ? (
              <div className="center">
                {store.login.username}ÎãòÏùÄ ÌòÑÏû¨ <span style={{ color: 'green' }}>Î°úÍ∑∏Ïù∏</span> ÎêòÏñ¥ÏûàÏäµÎãàÎã§ üòÑ
                <br />
                <span style={{ fontSize: '0.8rem' }}>(Î°úÍ∑∏Ïù∏ÏùÄ ÏµúÎåÄ ÏùºÏ£ºÏùºÍ∞Ñ Ïú†ÏßÄÎê©ÎãàÎã§.)</span>
              </div>
            ) : (
              <div className="login center">
                Ïû¨ Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§
                <Link to="/login" className="loginLink">
                  Î°úÍ∑∏Ïù∏
                </Link>
              </div>
            )}
          </>
        ) : (
          <>
            {(buttonName === 'Î°úÍ∑∏Ïù∏' || buttonName === 'ÌöåÏõêÍ∞ÄÏûÖ') && (
              <div className="oAuth">
                <div className="login">
                  {/* ÎÑ§Ïù¥Î≤Ñ Î°úÍ∑∏Ïù∏ */}
                  <div className="naverLogin no-drag" onClick={naverLogin}>
                    <img alt="" src={process.env.PUBLIC_URL + '/images/naver.png'} />
                    Log in with Naver
                  </div>
                  {/* Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ */}
                  <div className="naverLogin kakaoLogin no-drag" onClick={kakaoLogin}>
                    <img alt="" src={process.env.PUBLIC_URL + '/images/kakao.png'} />
                    Log in with Kakao
                  </div>
                  {/* Íµ¨Í∏Ä Î°úÍ∑∏Ïù∏ */}
                  <div className="naverLogin googleLogin no-drag" onClick={googleLogin}>
                    <img alt="" src={process.env.PUBLIC_URL + '/images/google.png'} />
                    Log in with Google
                  </div>
                  <div className="googleWarning">
                    Íµ¨Í∏Ä Î°úÍ∑∏Ïù∏ÏùÄ <span style={{ color: 'red' }}>Ïù∏Ïï± Î∏åÎùºÏö∞Ï†Ä(Ïπ¥Ïπ¥Ïò§ÌÜ° Îì±)</span>ÏóêÏÑú ÏßÄÏõêÎêòÏßÄ ÏïäÏäµÎãàÎã§. Ïò§Î•ò Î∞úÏÉù Ïãú ÎçîÎ≥¥Í∏∞(
                    <MoreVertIcon />, <img alt="" src={process.env.PUBLIC_URL + '/images/share.png'} />
                    )Î•º ÎàåÎü¨ÏÑú <span>Îã§Î•∏ Î∏åÎùºÏö∞Ï†Ä(Chrome, Safari Îì±)</span>ÏóêÏÑú Ïã§ÌñâÌï¥Ï£ºÏÑ∏Ïöî.
                  </div>
                </div>
              </div>
            )}
            <form className="login">
              <div className="text">ÏïÑÏù¥Îîî</div>
              {buttonName === 'ÌöåÏõêÌÉàÌá¥' ? <div>{store.login.username}</div> : <input name="username" onChange={changeUsername} />}

              <div className="check">{checkUsername}</div>

              <div className="text">ÎπÑÎ∞ÄÎ≤àÌò∏</div>
              <input name="password" type="password" onChange={changePassword} autoComplete="currnet-password" />
              <div className="check">{checkPassword}</div>

              {buttonName === 'ÌöåÏõêÍ∞ÄÏûÖ' && (
                <>
                  <div className="text">ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</div>
                  <input name="passwordConfirm" type="password" onChange={changePasswordConfirm} autoComplete="currnet-password" />
                  <div className="check" id="checkPasswordConfirm">
                    {checkPasswordConfirm}
                  </div>
                </>
              )}

              {/* Î≤ÑÌäº */}
              {buttonName === 'ÌöåÏõêÌÉàÌá¥' ? (
                <button style={{ background: 'red' }} onClick={clickButton}>
                  {buttonName}
                </button>
              ) : (
                <button onClick={clickButton}>{buttonName}</button>
              )}

              {/* Î°úÍ∑∏Ïù∏ or ÌöåÏõêÍ∞ÄÏûÖ */}
              {buttonName === 'Î°úÍ∑∏Ïù∏' && (
                <div className="loginLink">
                  <Link to="/register">ÌöåÏõêÍ∞ÄÏûÖ</Link>
                </div>
              )}
              {buttonName === 'ÌöåÏõêÍ∞ÄÏûÖ' && (
                <div className="loginLink">
                  <Link to="/login" className="loginLink">
                    Î°úÍ∑∏Ïù∏
                  </Link>
                </div>
              )}

              {browser === 'safari' && (
                <div className="text" style={{ fontSize: '0.8rem', textAlign: 'center' }}>
                  (SafariÏùò ÏÑ§Ï†ïÏùÑ Î≥ÄÍ≤ΩÌï¥Ïïº Î°úÍ∑∏Ïù∏Ïù¥ Í∞ÄÎä•Ìï©ÎãàÎã§.)
                  <br />
                  ÏÑ§Ï†ï ‚Üí Safari ‚Üí Í∞úÏù∏ Ï†ïÎ≥¥ Î≥¥Ìò∏ Î∞è Î≥¥Ïïà ‚Üí <br />
                  ÌÅ¨Î°úÏä§ ÏÇ¨Ïù¥Ìä∏ Ï∂îÏ†ÅÎ∞©ÏßÄ OFF, Î™®Îì† Ïø†ÌÇ§ Ï∞®Îã® OFF)
                </div>
              )}
            </form>
          </>
        )}
      </div>
    </div>
  )
}
export default React.memo(Login)
